<template>
  <div>
    <el-button
      class="add-button-add"
      size="small"
      type="primary"
      @click="dialogFormVisible = true"
      >添加</el-button
    >
    <el-dialog title="添加信息" :visible.sync="dialogFormVisible">
      <el-form :v-model="form">
        <el-form-item label="库位">
          <el-input v-model="form.location" class="addContent"></el-input>
        </el-form-item>
        <el-form-item label="绑定ip">
          <el-input v-model="form.ip" class="addContent"></el-input>
        </el-form-item>
        <el-form-item label="库位类型">
          <el-select
            v-model="form.type"
            placeholder="请选择库位类型"
            @change="addifLocation()"
            class="addContent"
          >
            <el-option label="放货库位" value="deliveryLocation"></el-option>
            <el-option label="取货库位" value="takeLocation"></el-option>
            <el-option label="辅助点" value="auxiliary"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="绑定辅助点">
          <el-select
            v-model="form.auxiliarylocation"
            placeholder="请选择辅助点"
            class="addContent"
            :disabled="isDisabled"
          >
            <el-option
              v-for="item in locations"
              :key="item.location"
              :label="item.location"
              :value="item.location"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="插齿高度">
          <el-input v-model="form.teethH" class="addContent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="clear">取 消</el-button>
        <el-button type="primary" @click="onSubmit">添加</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
export default {
  data() {
    return {
      dialogFormVisible: false,
      isDisabled: true,
      dtauxiliary: "00",
      locations: [],
      form: {
        location: "",
        ip: "",
        type: "",
        auxiliarylocation: "",
        teethH: 0.5,
        createTime: "",
      },
    };
  },
  methods: {
    clear() {
      this.locations = [];
      this.form = {
        location: "",
        ip: "",
        type: "",
        auxiliarylocation: "",
        teethH: 0.5,
        createTime: "",
      };
      this.dialogFormVisible = false;
      this.isDisabled = true;
    },
    onSubmit() {
      var date1 = dayjs(new Date().getTime() - 24 * 60 * 60 * 1000).format(
        "YYYY-MM-DD HH:mm:ss"
      );
      this.$axios
        .post("http://localhost:52000/web/location/add", {
          location: this.form.location,
          ip: this.form.ip,
          type: this.form.type,
          auxiliarylocation: this.form.auxiliarylocation,
          teethH: this.form.teethH,
          createTime: date1,
        })
        .then((resp) => {
          if (resp && resp.data.code === 200) {
            this.$emit("onSubmit");
          }
          this.clear();
          this.$emit("refresh");
        });
    },
    addLoadLocations() {
      this.locations = [];
      this.$axios
        .get("http://localhost:52000/web/location/findbytype?type=auxiliary", {})
        .then((resp) => {
          this.locations = resp.data.data;
        });
    },
    addifLocation() {
      if (this.form.type === "deliveryLocation") {
        this.isDisabled = false;
        this.dtauxiliary = "10";
        this.addLoadLocations();
      } else if (this.form.type === "takeLocation") {
        this.isDisabled = false;
        this.dtauxiliary = "01";
        this.addLoadLocations();
      } else {
        this.isDisabled = true;
      }
    },
  },
};
</script>

<style>
.add-button-add {
  width: 74px;
  height: 32px;
}

.addContent {
  float: left;
}
</style>


